}
.first{
   
    min-height:100%;
    background:linear-gradient(0deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3)), url('../images/logo-2.png');
    background-size:cover;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;

}
.disap:hover{
