}
.jumbotron{
 
    background:linear-gradient(0deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.3));
    margin-bottom: 10%;
    line-height: 30px;
    letter-spacing: 5px;
    word-spacing: 5px;
    padding: 2%;

    
}

.smol{
    height: 100%;
    line-height: 30px;
