    word-spacing: 5px;
    padding: 2%;

}
/*for responsiveness; manual adjustments*/
@media screen and(max-width: 1086px) {
 
    .jumbotron img{
        padding-left: 0;
    }
}
@media screen and(max-width: 786px) {
